import React from 'react';
import styled from 'styled-components';
import HomeShow from '../../components/HomeShow';
import quote_background from "../../assets/images/quote-background.png";
import best_image from "../../assets/images/best-image.png";
import image_section from "../../assets/images/image-section.png";
import contact_hero from "../../assets/images/contact-hero.png";
import a from "../../assets/images/11.png";
import b from "../../assets/images/22.png";
import a_image from "../../assets/images/1.png";
import b_image from "../../assets/images/2.png";
import c_image from "../../assets/images/3.png";
import d_image from "../../assets/images/4.png";
import e_image from "../../assets/images/5.png";
import f_image from "../../assets/images/6.png";
import { useTranslation } from 'react-i18next';

const HomeWrapper = styled.div`
    width: 100%;
`
const Twist = styled.div`
    width: 100%;
    padding:26px 20px;
    background-color: ${({theme}) => theme.bg2};
    color: ${({theme}) => theme.text1};
    text-align: center;
    font-size: 24px;
    font-weight: 100;
    b{
        font-weight: 700;
    }
    ${({ theme }) => theme.mediaWidth.upToMedium`
        font-size: 18px;
        line-height: 40px;
        padding: 10px 0px;
    `};
`
const InstructionsContainer =styled.div`
    width: 100%;
    background-color: ${({theme}) => theme.bg1};
    padding-top: 70px;
    padding-bottom: 50px;
    text-align: center;
    h2{
        color: ${({theme}) => theme.text2};
        margin-bottom: 30px;
        font-size: 38px;
        font-weight: 200;
        line-height: 53px;
        b{
            font-weight: 700;
        }
    }
    ${({ theme }) => theme.mediaWidth.upToMedium`
        padding-top: 30px;
        padding-bottom: 30px;
        h2{
            margin-bottom: 20px;
            font-size: 24px;
            font-weight: 200;
            line-height: 40px;
            b{
                font-weight: 700;
            }
        }
    `};
`
const InstructionsText = styled.div`
    color: ${({theme}) => theme.text3};
    width: 50%;
    margin-left: 25%;
    margin-bottom: 30px;
    line-height: 26px;
    font-size: 18px;
    opacity: 0.8;
    ${({ theme }) => theme.mediaWidth.upToMedium`
        width: 80%;
        margin-left: 10%;
        font-size: 14px;
    `}
`
const Container = styled.section`
    width: 100%;
    height: 412px;
    box-sizing: border-box;
    padding: 120px 0px;
    background: url(${quote_background}) no-repeat;
    background-size: cover;
    background-position: center;
    color: ${({theme}) => theme.text1};
    font-size: 36px;
    font-weight: 200;
    line-height: 70px;
    p{
        max-width: 900px;
        text-align: center;
        margin: 0 auto;
        b{
            color: #ffa02f; 
            font-weight: 700 ;
        }
    }
    h6{
        text-align: center;
        font-size: 16px;
        margin-top: 30px;
    }
    ${({ theme }) => theme.mediaWidth.upToMedium`
        padding: 150px 20px;
        font-size: 20px;
        font-weight: 200;
        line-height: 47px;
        p{
            text-align: center;
            margin: 0 auto;
        }
    `}
`
const ContainerBest = styled.div`
    width: 100%;
    padding-top: 140px;
    padding-bottom: 100px;
    background-color: ${({theme}) => theme.bg3};
    ${({ theme }) => theme.mediaWidth.upToMedium`
        padding-top: 50px;
        padding-bottom: 50px;
    `}
`
const BestTitle = styled.div`
    width: 50%;
    margin-left: 25%;
    text-align: center;
    h3{
        color: ${({theme}) => theme.text1}; 
        margin-bottom: 30px;
        font-size: 40px;
        font-weight: 200;
        line-height: 53px;
        b{
            color: ${({theme}) => theme.text4}; 
            font-weight: 700 ;
        }
    }
    p{  
        color: ${({theme}) => theme.text5}; 
        font-size: 16px;
        font-weight: 200;
        line-height: 26px;

    }
    ${({ theme }) => theme.mediaWidth.upToMedium`
        width: 90%;
        margin-left: 5%;
        h3{
            margin-bottom: 15px;
            font-size: 20px;
            font-weight: 200;
            line-height: 25px;
        }
        p{
            font-size: 14px;
            line-height: 24px;
        }
    `}
`

const BestFlex = styled.div`
    display: flex;
    flex-direction: row;
    padding-top: 70px;
    color: #fff;
    max-width: 960px;
    margin: 0 auto;
    ${({ theme }) => theme.mediaWidth.upToMedium`
        padding:30px;
        flex-direction: row;
        flex-wrap:wrap;
    `}
`
const BestFlexLeft = styled.div`
    width: 33.33%;
    text-align: right;
    ${({ theme }) => theme.mediaWidth.upToMedium`
        width: 100%;
        text-align: left;
    `}
`
const BestFlexCenter = styled(BestFlexLeft)`
    text-align: center;
`
const BestFlexRight = styled(BestFlexLeft)`
    text-align: left;
`
const BestRow = styled.div`
    display: flex;
    flex-direction: row;
    justify-content:flex-end;
    min-height: 200px;
    ${({ theme }) => theme.mediaWidth.upToMedium`
        justify-content:space-between; 
        flex-flow:row-reverse;
    `}
`
const BestRowRight =styled.div`
    display: flex;
    flex-direction: row;
    justify-content:flex-end;
    min-height: 200px;
`
const BestRowText = styled.div`
    padding: 20px;
    h4{
        color: ${({theme}) => theme.text6}; 
        font-size: 20px;
        line-height: 27px;
        font-weight: 400;
        margin-bottom: 3px;
    }
    h5{
        color: ${({theme}) => theme.text5}; 
        font-size: 16px;
        line-height: 27px;
        font-weight: 400;
        margin-bottom: 3px;
    }
    p{
        color: ${({theme}) => theme.text5};
        font-size: 14px;
        line-height: 24px;
    }
`
const BestRowIcon = styled.div`
    padding-top: 24px;
`
const ContainerAlternative = styled(Container)`
    width: 100%;
    height: 722px;
    background: url(${image_section}) no-repeat;
    background-size: cover;
    background-position: center;
    ${({ theme }) => theme.mediaWidth.upToMedium`
        padding: 10px 20px;
        height: 300px;
    `}
    
`
const AlternativeRow = styled.div`
    width: 60%;
    margin: 0 auto;
    text-align: left;
   
    h3{
        margin-top: 120px;
        font-size: 44px;
    }
    h5{
        font-size: 32px;
        font-weight: 200;
        margin-top: 20px;
        margin-bottom: 20px;
    }
    button{
        background-color: ${({theme}) => theme.primary1};
        color: ${({theme}) => theme.text1};
        font-size: 14px;
        padding:15px 50px;
        cursor: pointer;
        &:hover {
            background-color: ${({ theme }) => theme.text2};
        }
    }
    ${({ theme }) => theme.mediaWidth.upToMedium`
        width: 90%;
        h3{
            margin-top: 40px;
            font-size: 22px;
        }
        h5{
            font-size: 18px;
            font-weight: 200;
            margin-top: 20px;
            margin-bottom: 20px;
        }
        button{
            padding:10px 30px;
        }
    `}
`

const ContainerConnect = styled.div`
    width: 100%;
    height: 940px;
    background: url(${contact_hero}) no-repeat;
    background-size: cover;
    background-position: center;
    color:#fff;
    font-size: 1.4rem;
    text-align: center;
    position: relative;
    p{
        width: 50%;
        margin: 30px auto;
        line-height: 25px;
    }
    ${({ theme }) => theme.mediaWidth.upToMedium`
        p{
            width: 90%;
            margin: 20px auto;
            line-height: 25px;
        }
    `}
    
`

const ConnectTitle = styled.div`
    color: ${({theme}) => theme.text1}; 
    font-size: 38px;
    line-height: 53px;
    text-align: center;
    padding-top: 150px;
    ${({ theme }) => theme.mediaWidth.upToMedium`
        padding-top: 50px;
    `}
`

const ContainerInfoStyle = styled.div`
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    width: 50%;
    margin: 0 auto;
    ${({ theme }) => theme.mediaWidth.upToMedium`
        width: 90%;
    `}
`
const ContainerInfo = styled.div`
    width: 40%;
    margin-left: 4%;
    background-color: rgba(255, 255, 255, 0.07);
    padding: 15px 20px 30px;
    text-align: center;
    p{
        width: 80%;
        margin: 20px auto;
        line-height: 25px;
    }
    ${({ theme }) => theme.mediaWidth.upToMedium`
        width: 80%;
        margin: 20px auto;
    `}
    
`
const ContainerImgStyle = styled.div`
    text-align: center;
`
const Copyright = styled.div`
    color: ${({theme}) => theme.text5}; 
    font-size: 1.4rem;
    position: absolute;
    bottom: 20px;
    left: 40%;
    right: 40%;
    ${({ theme }) => theme.mediaWidth.upToMedium`
        left: 20%;
        right: 20%;
    `}
`




export default function Home() {
    
    const { t  } = useTranslation();
    // {t('homeScreen')}
    return (
        <HomeWrapper>
            <HomeShow />
            <Twist>{t('We_are_here_to')} <b>{t('Revolutionize_the_World')}</b></Twist>
            <InstructionsContainer>
                <h2><b>{t('Vision')}</b> {t('of_PokerFi_Market')}</h2>
                <InstructionsText>
                {t('PokerFi_introduce')}
                </InstructionsText>
            </InstructionsContainer>
            <Container>
                <p><b>{t('your_Poker_introduce_1')}</b>,{t('your_Poker_introduce_2')}</p>
            </Container>
            <ContainerBest>
                <BestTitle>
                    <h3>{t('What_We_do')}<b>{t('We_do_Best')}</b></h3>
                    <p>{t('We_do_PokerFi')}</p>
                </BestTitle>
                <BestFlex> 
                    <BestFlexLeft>
                        <BestRow>
                            <BestRowText>
                                <h4>{t('Our_Goal')}</h4>
                                <h5>{t('block_height')}8575722{t('open')}</h5>
                                <p>{t('Our_Goal_introduce')}</p>
                            </BestRowText>
                            <BestRowIcon>
                                <img width={'50px'}  src={a_image} alt='best' />
                            </BestRowIcon>
                        </BestRow>
                        <BestRow>
                            <BestRowText>
                                <h4>{t('Who_are_We')}</h4>
                                <h5>{t('block_height')}8662122{t('open')}</h5>
                                <p>{t('Who_are_We_introduce')}</p>
                            </BestRowText>
                            <BestRowIcon>
                                <img width={'50px'}  src={b_image} alt='best' />
                            </BestRowIcon>
                        </BestRow>
                        <BestRow>
                            <BestRowText>
                                <h4>{t('Environmental_Impact')}</h4>
                                <h5>{t('block_height')}8748522{t('open')}</h5>
                                <p>{t('Environmental_Impact_introduce')}</p>
                            </BestRowText>
                            <BestRowIcon>
                                <img width={'50px'}  src={c_image} alt='best' />
                            </BestRowIcon>
                        </BestRow>
                    </BestFlexLeft>
                    <BestFlexCenter>
                        <img width={'76%'}  src={best_image} alt='best' />
                    </BestFlexCenter>
                    <BestFlexRight>
                        <BestRowRight>
                            <BestRowIcon>
                                <img width={'50px'}  src={d_image} alt='best' />
                            </BestRowIcon>
                            <BestRowText>
                                <h4>{t('Principle')}</h4>
                                <h5>{t('block_height')}8834922{t('open')}</h5>
                                <p>{t('Principle_introduce')}</p>
                            </BestRowText>
                        </BestRowRight>
                        <BestRowRight>
                            <BestRowIcon>
                                <img width={'50px'}  src={e_image} alt='best' />
                            </BestRowIcon>
                            <BestRowText>
                                <h4>{t('Mining_for_Everyone')}</h4>
                                <h5>{t('block_height')}8921322{t('open')}</h5>
                                <p>{t('Mining_for_Everyone_introduce')}</p>
                            </BestRowText>
                        </BestRowRight>
                        <BestRowRight>
                            <BestRowIcon>
                                <img width={'50px'}  src={f_image} alt='best' />
                            </BestRowIcon>
                            <BestRowText>
                                <h4>{t('Our_Token')}</h4>
                                <h5>{t('block_height')}9007722{t('open')}</h5>
                                <p>{t('Our_Token_introduce')}</p>
                            </BestRowText>
                        </BestRowRight>
                    </BestFlexRight>
                </BestFlex>
            </ContainerBest>
            <ContainerAlternative>
                <AlternativeRow>
                    <h3>{t('Alternative_Form_of_Mining')}</h3>
                    <h5>{t('Acquire_PokerFi_Today')}</h5>
                    <button onClick={()=>window.location.href='https://home.antminer.world/'}>{t('Get_it_Now')}</button>
                </AlternativeRow>
            </ContainerAlternative>
            <ContainerConnect>
                <ConnectTitle>{t('Let')} <b>{t('Connect')}</b></ConnectTitle>
                <p>{t('Connect_introduce')}</p>
                <ContainerInfoStyle>
                    <ContainerInfo>
                        <ContainerImgStyle>
                            <img width={'58px'} src={a} alt='1' />
                        </ContainerImgStyle>
                        <p>{t('Connect_1')}</p>
                    </ContainerInfo>
                    <ContainerInfo>
                        <ContainerImgStyle>
                            <img width={'58px'} src={b} alt='2' />
                        </ContainerImgStyle>
                        <p>{t('Connect_2')}</p>
                    </ContainerInfo>
                </ContainerInfoStyle>
                <Copyright>Copyright © 2021 - ANTSwap</Copyright>
            </ContainerConnect> 
        </HomeWrapper>
    )
}
