import Home from '../pages/home'
import Dig from '../pages/dig'
import Mall from '../pages/mall'

const routes = [
    {
        path: "/",
        exact: true,
        component: Home
    },
    {
        path: '/home',
        exact: true,
        component: Home
    },
    {
        path: '/dig',
        exact: true,
        component: Dig
    },
    {
        path: '/mall',
        exact: true,
        component : Mall
    }
]

export default routes;